import React, { Component } from 'react';
import {
  NavLink
} from 'react-router-dom';


class Nav extends Component {
  constructor(props){
    super(props);
    this.state = {
      width:'2',
      height:'0',
      rot:'0',
      headOpacity:'1',
    }
    this.handleMenuOpen = this.handleMenuOpen.bind(this);
    this.handleMenuClose = this.handleMenuClose.bind(this);

  }


  handleMenuOpen(event){
    const {width} = this.state;
    this.setState({rot:'360'})
      const hei = setInterval(()=>{
        this.setState({height:'210'})
        if (window.innerWidth <= 768) {
          this.setState({headOpacity:"0.3"})
        }
        clearInterval(hei);
      },200)
    const wid = setInterval(()=>{
      width === '2'
      ? this.setState({width:'200'})
      : this.setState({width:'2'})
      clearInterval(wid);
    },400);
  }

  handleMenuClose(event){
    const {height} = this.state;
    this.setState({width:'2'})
    const hei = setInterval(()=>{
      height === '0'
        ? this.setState({height:'210'})
        : this.setState({height:'0'})
        if (window.innerWidth <= 768) {
          this.setState({headOpacity:"1"})
        }

        clearInterval(hei);
      },100);
    const wid = setInterval(() =>{
        this.setState({rot:'0'})
        clearInterval(wid);
      },200);
  }

  render() {
    const {height} = this.state;
    return (

      <nav style={{transform:`rotateZ(${this.state.rot}deg)`}}>
      <div className='top-menu' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>
      <div className='salate' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>
      <div className='meat' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>
      <div className='menu'  style={{height:`${height}px`,width:`${this.state.width}px` }}>
      <NavLink activeClassName="active" to='/pocetna' className='nav-link'>Pocetna</NavLink>
      <NavLink activeClassName="active" to='/meni' className='nav-link'>Meni</NavLink>
      <NavLink activeClassName="active" to='/specijaliteti' className='nav-link'>Specijaliteti</NavLink>
      <NavLink activeClassName="active" to='/kontakt' className='nav-link'>Kontakt</NavLink>
      <NavLink activeClassName="active" to='/onama' className='nav-link'>O nama</NavLink>
      </div>
      <div className='salate' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>
      <div className='bottom-menu' onClick={height ==='0' ? this.handleMenuOpen: this.handleMenuClose}></div>
      </nav>

    );
  }

}

export default Nav;



